import React from "react"
import Button from "../../../../Components/Button";
import correctingIcon from "../../../../Components/images/icon-correcting.svg";
import checkIcon from "../../../../Components/images/icon-checkbox-green.png"


class  ProfileInputName extends React.Component {

    constructor(props) {
        super(props);
        this.inputRef = React.createRef();
        this.state = {
            isInputCorrecting: false,
            name: this.props.userName,
            error_name: '',
        };

         this.handleNickChange = this.handleNickChange.bind(this);
         this.handleSubmitNick = this.handleSubmitNick.bind(this);
    }


    inputCorrecting (event){
        event.preventDefault();
        this.setState({isInputCorrecting: true});


        this.inputRef.current.focus();
        console.log("this.inputRef.current", document.activeElement);
    }

    inputFinishCorrecting (event){
        event.preventDefault();
        this.setState({isInputCorrecting: false})
    }




    handleNickChange(event) {
        this.setState({name: event.target.value});
    }

    handleSubmitNick(event) {
        event.preventDefault();
        // console.log("this.state.name =", this.state.name);

        if (this.state.name === this.props.userName){
            this.setState({isInputCorrecting: false});
            return;
        }


        if( (this.state.name === "") || (this.state.name === undefined) ){
            this.setState({isInputCorrecting: false});
            this.setState({ name: this.props.userName });
            this.setState({error_name:"–í–∞—à nickname –Ω–µ –º–æ–∂–µ—Ç –±—ã—Ç—å –ø—É—Å—Ç—ã–º"});

            return;
        }
        if( (this.state.name.length <= 3) ){
            this.setState({error_name:"Nickname –Ω–µ –º–æ–∂–µ—Ç –±—ã—Ç—å > 4 —Å–∏–º–≤–æ–ª–æ–≤"});

            return;
        }

        if( (this.state.name.length > 20) ){
            this.setState({error_name:"Nickname –Ω–µ –º–æ–∂–µ—Ç –±—ã—Ç—å < 20 —Å–∏–º–≤–æ–ª–æ–≤"});

            return;
        }

        setTimeout(
            () => this.setState({error_name:""}),
            1500
        );

        alert('–û—Ç–ø—Ä–∞–≤–ª–µ–Ω–Ω–æ–µ –∏–º—è: ' + this.state.name);
    }

    handleFullNameFocus(){

        this.setState({isInputCorrecting: true});

        if(window.innerWidth < 820) {
            this.props.hideMainMenu();
            document.body.classList.add("FocusOnInput");
        }
    }
    handleFullNameBlur(){

        if( (this.state.name === "") || (this.state.name === undefined) ){
            this.setState({ name: this.props.userName });
        }

        if(window.innerWidth < 820){
            this.props.showMainMenu();
            document.body.classList.remove("FocusOnInput");
        }
    }


    render (){



        return(
            <form className="profile_form">
                <label htmlFor="userName" className="label_element">
                    <input id="userName" className="profile_form__input"

                           type="text"
                           name="name"
                           value={this.state.name}
                           onChange={(e)=>this.handleNickChange(e)}

                           // disabled={ !this.state.isInputCorrecting}
                           ref={this.inputRef}
                           onFocus={(e) => this.handleFullNameFocus(e)}
                           onBlur={(e) => this.handleFullNameBlur(e)}
                    />


                    <span className="text_error">{this.state.error_name}</span>
                </label>

                {
                    this.state.isInputCorrecting
                        ?
                        <Button className="profile_form__change" type="submit" onClick={(e) => this.handleSubmitNick(e)}>
                            <img src={checkIcon} alt="–ø—Ä–∏–º–µ–Ω–∏—Ç—å –Ω–∏–∫"/>
                        </Button>
                        :
                        <Button className="profile_form__change" type="button" onClick={(e) => this.inputCorrecting(e)}>
                             <img src={correctingIcon} alt="–∏–∑–º–µ–Ω–∏—Ç—å –Ω–∏–∫"/>
                        </Button>
                }
            </form>
        );
    }
}

export default ProfileInputName