import React from "react"
import Button from "../../../../Components/Button";
import correctingIcon from "../../../../Components/images/icon-correcting.svg";


class ProfileInputTelegram extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            telegram: this.props.userTelegram,
            error_telegram: '',
        };

        this.handleTelegramChange = this.handleTelegramChange.bind(this);
        this.handleSubmitTelegram = this.handleSubmitTelegram.bind(this);
    }

    handleTelegramChange(event) {
        console.log("Telegram was chenged", event.target.value);
        this.setState({telegram: event.target.value});
    }
    handleSubmitTelegram(event) {
        event.preventDefault();

        if( (this.state.telegram === "") || (this.state.telegram === undefined) ){
            this.setState({error_telegram:"–ü–æ–∂–∞–ª—É–π—Å—Ç–∞, –≤–≤–µ–¥–∏—Ç–µ Telegram"});
            this.setState({telegram: this.props.telegram });
            return;
        }


        this.setState({error_telegram:""});
        alert('–û—Ç–ø—Ä–∞–≤–ª–µ–Ω–Ω–æ–µ Telegram: ' + this.state.telegram);
    }

    handleFullNameFocus(){
        if(window.innerWidth < 820) {
            this.props.hideMainMenu();
            document.body.classList.add("FocusOnInput");
        }
    }
    handleFullNameBlur(){
        if(window.innerWidth < 820){
            this.props.showMainMenu();
            document.body.classList.remove("FocusOnInput");
        }
    }



    render() {

        return (
            <form className="profile_form"  onSubmit={(event) => this.handleSubmitTelegram(event)}>
                <label htmlFor="userTelegram" className="label_element">
                    <input id="userTelegram"
                           className="profile_form__input"
                           type="text"
                           name="telegram"
                           value={this.state.telegram}
                           onChange={(e)=>this.handleTelegramChange(e)}


                           onFocus={(e) => this.handleFullNameFocus(e)}
                           onBlur={(e) => this.handleFullNameBlur(e)}
                    />
                    <span className="text_error">{this.state.error_telegram}</span>
                </label>
                <Button className="profile_form__change" type="submit">
                    <img src={correctingIcon} alt="–∏–∑–º–µ–Ω–∏—Ç—å Telegram"/>
                </Button>
            </form>
        );
    }
}

export default ProfileInputTelegram