import React from 'react';
import Button from './../../Components/Button'

import "./games.scss"
import parse from 'html-react-parser'
import {withRouter} from "react-router-dom";


const GamePage = withRouter((props) => {
    const {games, playGame} = props;



        // const game = games.get(
    //         //     parseInt(props.match.params.id, 10)
    //         // );

        // console.log("games", games);

    // TODO –ü–µ—Ä–µ–¥–µ–ª–∞—Ç—å... —Ç—É—Ç –≤—Å–µ–≥–¥–∞ –æ—Ç–¥–∞–µ—Ç—å—Å—è –ø–µ—Ä–≤–∞—è –∑–∞–ø–∏—Å—å
    const first = games[0];
    const gameData = {
        gameName: first.gameName,
        gamePlatform: first.gamePlatform,
        gameScreen: first.gameScreen,
        gameScreenMiddle: first.gameScreenMiddle,

        gameCharacteristics: first.gameCharacteristics,
        gameSeo: first.gameSeo,
        iframeGame: first.iframeGame,
    };



    return (
        <div className="game_block">
            <div className="game_block__top">
                <picture className="game_picture">
                    <source media="(max-width: 768px)" srcSet={gameData.gameScreenMiddle}/>
                    <img src={gameData.gameScreen} alt={gameData.gameName}/>
                </picture>
                <div className="game_block_navigation">
                    <h1 className="game_name">{gameData.gameName}</h1>
                    <p className="game_platform">{gameData.gamePlatform}</p>
                    <Button className="btn btn_playGame" onClick={playGame}>–ò–≥—Ä–∞—Ç—å</Button>
                </div>
            </div>
            <div className="game_info">
                <p className="game_info__title">–•–∞—Ä–∞–∫—Ç–∏—Ä–∏—Å—Ç–∏–∫–∏</p>
                <ul className="game_info__all">
                    <li className="game_info__item" key={1}>
                        <span>–ö–æ–ª–∏—á–µ—Å—Ç–≤–æ –ª–∏–Ω–∏–π</span>
                        <span>{gameData.gameCharacteristics.lines}</span>
                    </li>
                    <li className="game_info__item" key={2}>
                        <span>–ö–æ–ª–∏—á–µ—Å—Ç–≤–æ –±–∞—Ä–∞–±–∞–Ω–æ–≤</span>
                        <span>{gameData.gameCharacteristics.drums}</span>
                    </li>
                    <li className="game_info__item" key={3}>
                        <span>–ú–∏–Ω–∏–º–∞–ª—å–Ω–∞—è —Å—Ç–∞–≤–∫–∞</span>
                        <span>{gameData.gameCharacteristics.minBet}</span>
                    </li>
                    <li className="game_info__item" key={4}>
                        <span>–ú–∞–∫—Å–∏–º–∞–ª—å–Ω–∞—è —Å—Ç–∞–≤–∫–∞</span>
                        <span>{gameData.gameCharacteristics.maxBet}</span>
                    </li>
                    <li className="game_info__item" key={5}>
                        <span>–†–∏—Å–∫ –∏–≥—Ä–∞</span>
                        <span>{gameData.gameCharacteristics.riskGame}</span>
                    </li>
                    <li className="game_info__item" key={6}>
                        <span>–§—Ä–∏-—Å–ø–∏–Ω—ã</span>
                        <span>{gameData.gameCharacteristics.freeSpins}</span>
                    </li>
                    <li className="game_info__item" key={7}>
                        <span>–ë–æ–Ω—É—Å–Ω–∞—è –∏–≥—Ä–∞</span>
                        <span>{gameData.gameCharacteristics.bonusGame}</span>
                    </li>
                </ul>
            </div>

            <div className="game_seo">
                {parse(gameData.gameSeo)}
            </div>

        </div>

    )
});

export default GamePage;