import React from "react";
import Slider from "react-slick";


import SliderItem from "./SliderItem/SliderItem"
import "slick-carousel/slick/slick.css";


export default function SimpleSlider() {
    var settings = {
        dots: false,
        infinite: false,
        speed: 500,
        slidesToShow: 2.5,
        slidesToScroll: 1,
        arrows: false,
        responsive: [
            {
                breakpoint: 1200,
                settings: {
                    slidesToShow: 2,
                }
            },
            {
                breakpoint: 991,
                settings: {
                    slidesToShow: 1.5,
                }
            },
            {
                breakpoint: 650,
                settings: {
                    slidesToShow: 1.2,
                }
            },
            {
                breakpoint: 490,
                settings: {
                    slidesToShow: 1.1,
                }
            },

        ]
    };
    return (
        <Slider className="slider_all" {...settings}>
            <SliderItem key={'1'}/>
            <SliderItem key={'2'}/>
            <SliderItem key={'3'}/>
            <SliderItem key={'4'}/>
        </Slider>
    );
}
